@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
}

.app {
  background-color: steelblue;
  text-align: center;
  display: grid;
  grid-template-areas:
    "h h h h h h h h h h h h"
    "n n n n n n n n n n n n"
    "c c c c c c c c c c c c"
    "f f f f f f f f f f f f";
  grid-gap: 10px;
}

.header {
  grid-area: h;
}

.mynavbar {
  grid-area: n;
}

.content {
  grid-area: c;
  min-height: 60vh;
}

.footer {
  grid-area: f;
  margin-top: 25px;
}

#logotext {
  font-family: "Roboto Mono", monospace;
  font-weight: 900;
  color: white;
}

.lead {
  color: white;
}

.logomarg {
  margin-top: 20px;
}

button {
  margin-left: 15px;
}
span {
  color: white;
  margin-left: 10px;
}

.register {
  width: 50%;
  display: block;
  margin: auto;
  padding: 10px;
}

.register input {
  margin: 15px 0 12px;
}

/* .register input:invalid {
    box-shadow: inset 0px 0px 0px 0px;
} */

.register label {
  margin-left: 7px;
}

.register span {
  color: black;
  font-size: 16px;
  display: block;
  margin-top: -7px;
  word-wrap: break-word;
}

.register button {
  margin-top: 15px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
}

.contact span {
  color: #ddd;
  font-size: 24px;
}

.products img{
    text-align: center;
    padding: 20px;
}


.products button {
    position:abosulute;
    top: 0px;
    left: 1200px;
}

.homepage {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}

.homepage img {
  border-radius: 30px;
  padding: 20px;
}

.homepage p {
  padding: 20px 90px 10px 90px;
}

.homeicons span {
  color: black;
  display: inline-block;
  padding: 20px;
  margin: 0;
  font-size: 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.homeicons i {
  margin-right: 5px;
}

.footer i {
  padding: 0 5px 0 5px;
}
.footer {
  background-color: #363c3d;
  color: #eee;
  padding-top: 15px;
}

